import React from 'react';
import '../store.css';

const ProductList = ({categories, setCategoryHandler}) => {


  return (
    <div className='product-list' >
        <h6 className='list-title' >Shop By Category</h6>
        {categories.map(x => 
        <div>
            <h6 className='product-title' onClick={()=>setCategoryHandler(x)} >{x.name}</h6>
        </div> )}
    </div>
  );
}

export default ProductList;